* {
  margin: 0;
  padding: 0;
}
body {
  height: 100vh;
  background: #8cccf4;
}
#close-btn:hover {
  background-color: #ffc107 !important;
  color: crimson;
}
.notes-card {
  max-width: 250px;
  max-height: 300px;
  background: #f7f7f7;
  border-radius: 6px;
  box-shadow: 0 0 22px gray;
}

@media (max-width: 500px) {
  .box {
    width: 100% !important;
  }
}
